          text: 'Sobre isso eu tambm s posso falar de outiva; porm nada me impede de comunicar-vos o que sei. Talvez, mesmo, seja a quem se encontra no ponto de imigrar para o outro mundo que compete investigar acerca dessa viagem e dizer como ser preciso imagin-la. Que melhor coisa se poder fazer para passar o tempo at sol baixar?',
