          text: 'Ento, o que importa  no desanimares, disse;  possvel que ainda venhas a ouvi-las. Talvez te parea estranho que entre todos os casos seja este o nico simples e que no comporte como os demais, decises arbitrrias, segundo as circunstncias, a saber: que  melhor estar morto do que vivo. E havendo pessoas para quem a morte, de fato,  prefervel, no sabers dar a razo de ser vedado aos homens procurarem para si mesmos semelhante benefcio, mas precisarem esperar por benfeitor estranho.',
