          text: 'Ao que Smias observou: Porm quer parecer-me, Scrates, que h bastante senso nas palavras de Cebete. No se compreende, de fato, que indivduos verdadeiramente sbios fujam de amos melhores do que eles e se alegrem com essa liberdade. A meu ver, o argumento de Cebete vai dirigido contra ti, por aceitares  ligeira a idia de deixar-nos, e tambm aos amos cuja superioridade s o primeiro a proclamar.',
          footnote: [],
