          text: 'Deixa-o, disse. E agora, juzes, pretendo expor-vos as razes de estar convencido de que o indivduo que se dedicou a vida inteira  Filosofia, ter de mostrar-se confiante na hora da morte, pela esperana de vir a participar, depois de morto, dos mais valiosos bens. Como poder ser dessa maneira, Smias e Cebete,  o que tentarei explicar-vos.',
