          text: 'Smias, ento, rindo, Por Zeus, Scrates, interrompeu-o; fizeste-me rir, em que pese  minha falta de disposio para isso. O que penso  que, se os homens te ouvissem discorrer dessa maneira, achariam certo o que se diz dos filsofos  e nesse ponto contariam com a aprovao de nossa gente  que em verdade eles vivem a morrer, sabendo perfeitamente que outra coisa no merecem.',
