          text: 'Considera agora, meu caro, se pensas como eu. Estou certo de que desse modo ficaremos conhecendo melhor o que nos propomos investigar. s de opinio que seja prprio do filsofo esforar-se para a aquisio dos pretensos prazeres, tal como comer e beber?',
