          text: 'Essa  a razo, Smias, de, na opinio da maioria dos homens, no merecer viver o indivduo a quem nada disso  agradvel e que no se importa com tais prticas, por achar-se muito mais perto da condio de morto e por no dar a menor importncia aos prazeres alcanados por intermdio do corpo.',
