          text: 'Por conseguinte, companheiro, continuou Scrates, se tudo isso estiver certo, h muita esperana de que somente no ponto em que me encontro, e mais em tempo algum,  que algum poder alcanar o que durante a vida constitui nosso nico objetivo. Por isso, a viagem que me foi agora imposta deve ser iniciada com uma boa esperana, o que se dar tambm com quantos tiverem certeza de achar-se com a mente preparada e, de algum modo, pura.',
