          text: 'Mas, meu bem aventurado Smias, essa no  a maneira de alcanar a virtude, trocar uns prazeres por outros, tristezas, ou temores por temores de outras espcie, como trocamos em midos moeda de maior valor. S h uma moeda verdadeira, pela qual tudo isso deva ser trocado: a sabedoria. E s por troca com ela, ou com ela mesma,  que em verdade se compra ou se vende tudo isto: coragem, temperana e justia, numa palavra, a verdadeira virtude, a par da sabedoria, pouco importando que se lhe associem ou dela se afastem prazeres ou temores e tudo o mais da mesma natureza. Separadas da sabedoria e permutadas entre si, todas elas no so mais do que sombra de virtude, servis em toda a linha e sem nada possurem de verdadeiro nem so. A verdade em si consiste, precisamente, na purificao de tudo isso, no passando a temperana, a justia, a coragem e a prpria sabedoria de uma espcie de purificao.  muito provvel que os instituidores de nossos mistrios no fossem falhos de merecimento e que desde muitos nos quisessem dar a entender por meio de sua linguagem obscura que a pessoa no iniciada nem purificada, ao chegar ao Hades vai para um lamaal, ao passo que o iniciado e puro, ao chegar l passa a morar com os deuses. Porque, como dizem os que tratam dos mistrios: muitos so os portadores de tirso, porm pouqussimos os verdadeiros inspirados. E no meu modo de entender, so estes, apenas, os que se ocuparam com a filosofia, em sua verdadeira acepo, no nmero dos quais procurei incluir-me, esforando-me nesse sentido, por todos os modos, a vida inteira e na medida do possvel sem nada negligenciar. Se trabalhei como seria preciso e tirei disso algum proveito,  o que com segurana ficaremos sabendo no instante de l chegarmos, se Deus quiser, e dentro de pouco tempo, segundo creio. Eis a, Smias e Cebete, minha defesa, a razo de apartar-me nem revoltar-me, por estar convencido de que tanto l como aqui encontrarei companheiros e mestres excelentes. O vulgo no me dar crdito; porm se a minha defesa vos pareceu mais convincente do que aos meus juzes atenienses,  tudo o que posso desejar.',
