          text: 'Depois de haver Scrates assim falado Cebete tomou a palavra e disse: Scrates, tudo o que  alma, dificilmente os homens podero acreditar que, uma vez separada do corpo, venha ela a subsistir em  alguma parte, por destruir-se e desaparecer no mesmo dia em que o homem fenece. No prprio instante em que ele sai do corpo e dele sai, dispersa-se como sopro ou fumaa, evola-se, deixando, em conseqncia de existir em qualquer parte. Porque, se ela se recolhesse algures a si mesma, livre dos males que h pouco enumeraste, haveria grande e doce esperana de ser verdade, Scrates, tudo o que disseste. Mas o fato  que se faz mister de no pequeno poder de persuaso e de muitos argumentos para demonstrar que a alma subsista depois da morte do homem e que conserva alguma atividade e pensamento.',
