          text: 'Para deixar a questo mais fcil de entender, observou, no te limites a consider-la com relao aos homens, porm estende-a ao conjunto dos animais e das plantas, numa palavra, a tudo o que nasce, a fim de vermos se cada coisa no se origina exclusivamente do seu contrrio, onde quer que se verifique essa relao, tal como no caso do belo, que tem como contrrio o feio, no do justo e do injusto e em mil outro exemplos que se poderiam enumerar. Investiguemos, ento, se  foroso que tudo o que tenha algum contrrio de nada mais possa originar-se a no ser desse mesmo contrrio. Por exemplo: para ficar grande alguma coisa,  preciso que antes fosse pequena, sem o que no poderia aumentar.',
