          text: 'Vou falar de um dos pares de contrrios a que me referi h pouco, disse Scrates, e de suas respectivas geraes; tu te manifestars a respeito do outro. Denomino o primeiro, viglia e sono; da viglia nasce o sono, e vice-versa: do sono, a viglia, tendo um dos processos o nome de acordar e o outro o de dormir. Isso te basta, perguntou, ou no?',
