          text: 'A lua cheia que o vira marchar, levado no seu sonho de conquista e de fama, comeava a minguar, quando um dos seus cavaleiros apareceu, com as armas rotas, negro do sangue seco e do p dos caminhos, trazendo a amarga nova de uma batalha perdida e da morte do rei, trespassado por sete lanas entre a flor da sua nobreza,  beira de um grande rio.',
